@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One&family=Roboto+Condensed:wght@300&display=swap');

.textContainer {
  display: flex;
  flex-direction: column;
  margin-left: 100px;
  color: white;
  height: 80%;
  justify-content: space-between;
  
  background: rgba(36, 23, 23, 0.6);
border-radius: 16px;
box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(14.3px);
-webkit-backdrop-filter: blur(14.3px);
border: 1px solid rgba(255, 255, 255, 0.199);
  padding: 15px;
  border-radius: 20px;
  font-family: 'Roboto Condensed', sans-serif;
  rotate: 5deg;

}
.textContainer h4 {
  font-family: 'Black Ops One', cursive;
  font-size: 30px;
  color: rgb(220, 171, 107);
}

.activeContainer {
  display: flex;
  align-items: center;
  margin-bottom: 50%;
}

.activeItem {
  display: flex;
  align-items: center;
}

.activeContainer img {
  padding-left: 10px;
}

.textContainer h1 {
  margin-bottom: 0px;
}

@media (min-width: 320px) and (max-width: 1200px) {
  .textContainer {
    display: none;
  }
}