@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One&family=Roboto+Condensed:wght@300&display=swap');


.outerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background: #c31432;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #240b36, #c31432);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  
  
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #363636a1;
  border-radius: 20px;
  height: 85%;
  width: 45%;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 20px;
  
}

.ziggy{
  text-align: center;
  background: #c31432;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #240b36, #c31432);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  color:white;
  height: 100vh;
  margin-top: -50px;
  padding-top: 100px;
  
  
  
}

.ziggy h1 {
  font-family: 'Black Ops One', cursive;
  font-size: 40px;
  letter-spacing: 3px;
}

@media (min-width: 320px) and (max-width: 480px) {
  .outerContainer {
    height: 100%;
  }

  .container {
    width: 100%;
    height: 100%;
  }
}

@media (min-width: 480px) and (max-width: 1200px) {
  .container {
    width: 60%;
  }
}
